.catalog-page {
  display: flex;
}

.div-for__title__product-list {
  width: 100%;
}

.div-search-main-title {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
}

.catalog__search {
  width: 80%;
}

.catalog__none {
  display: none !important;
}

.catalog__btn-else {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.catalog__inner__btn-else {
  background-color: rgb(42, 113, 253);
  border: 1px solid transparent;
  color: white;
  padding: 10px 15px;
  cursor: pointer;
  transition: 0.3s ease;
}
.catalog__inner__btn-else:hover {
  background-color: white;
  color: rgb(42, 113, 253);
  border: 1px solid rgb(42, 113, 253);
  transition: 0.3s ease;
}

@media (max-width: 1000px) {
  .catalog-page {
    display: flex;
    flex-direction: column;
  }
  .catalog__main-title {
    display: none;
  }
  .div-search-main-title {
    margin-top: 20px;
    margin-bottom: 5px;
  }
  .catalog__search {
    width: 95%;
    font-size: 20px;
  }
}
